
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import scroll from '@/components/scroll/scroll'
import { types } from 'util';
export default{
components: {
XHeader,Toast,scroll
},
data(){
return {
noData: true,
pageIndex: 1,
list: [],
resultData: ['平和质','气虚质','阳虚质','阴虚质','痰湿质','湿热质','血瘀质','气郁质','特禀质'],
isloading: false,
curIndex: 0,
}
},
mounted(){
this.initData(true);
},
methods: {

getTime(CPSJ){
var date = new Date(CPSJ);
var Y = date.getFullYear()
var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1)
var D = date.getDate() + ' '
return '填表时间 '+Y+'-'+M+'-'+D;
},

getCPJGDM(CPJGDM){
if(typeof CPJGDM !='undefined'){
var listSplit = CPJGDM.replace(/0/g,'').split("|");
var dataString = ''
for (const key in listSplit) {
dataString+=this.resultData[parseInt(key)]+'、'
}
return dataString;

}else{
return '未知';
}


},

starActivity(RECORD_ID){
this.$router.push({name: "TCMListDetails", params: {RECORD_ID: RECORD_ID}});
},
/**
* @param upOrDown
* true 第一次加载或者刷新
* false 上拉加载
*/
initData(upOrDown) {
if (upOrDown) {
this.pageIndex = 1;
} else {
this.pageIndex++;
}

this.$ajax
.post("", {
"MSH.1": "lnrzyjkgl",
"MSH.2": "list",
'GRDAID':this.$root.loginInfo.grdaid,
'num': this.num,
'start': this.start,
'pageIndex': this.pageIndex,
'pageSize': "10",
'CHZBM': this.$root.loginInfo.CHZBM,
'grdaid': this.$root.loginInfo.grdaid,
'token':this.$root.loginInfo.token,
})
.then(data => {
if (typeof data.rows != "undefined" && data.rows.length > 0) {
console.log("------"+JSON.stringify(data))

this.noData = false;
this.list = this.list.concat(data.rows);
} else {
if (this.pageIndex > 1) {
this.$vux.toast.text("没有更多了", "bottom");
this.$refs.scroll.finishPullUp();
} else {
this.noData = true;
}
}
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
})
.catch(error => {
this.noData = true;
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
});
},

}
}



